<template>
  <article
    :class="{'border-red': balanceAtTheEndOfTheDay < 0}"
    class="card-panel grey lighten-2 calendar-list__card day-card">
    <header class="day-card__header">
      <div class="row valign-wrapper">
        <div class="col s12">
          <div class="center-align day-card__date">{{ dateFilter(todayFilter) }}</div>
        </div>
      </div>
      <div class="row valign-center blue lighten-3">
        <div class="col s12">
          <div class="center-align day-card__balance"><strong>Утром {{ currencyFilter(balanceTillToday) }}</strong>
          </div>
        </div>
      </div>
      <div v-if="(notPlannedRecordsSumTillToday + allCategoriesOverspendSumTillToday) > 0" class="valign-center">
        <div class="col s12 red white-text">
          <div class="center-align day-card__overspending">Не учтено
          </div>
          <div class="center-align day-card__overspending">
            {{ currencyFilter(notPlannedRecordsSumTillToday + allCategoriesOverspendSumTillToday) }}
          </div>
        </div>
      </div>
    </header>
    <section class="day-card__section day-card__section-income green lighten-4">
      <div class="center-align day-card__income-list-item"><strong>Доход {{
          currencyFilter(allIncomeRecordsSum)
        }}</strong></div>
    </section>
    <section class="day-card__section day-card__section-outcome orange lighten-4">
      <div class="center-align day-card__income-list-item"><strong>Расход {{
          currencyFilter(allOutcomeRecordsSum)
        }}</strong></div>
      <ul class="center-align day-card__income-list">
        <li
          v-for="cat in outcomeCategoriesWithSum"
          :key="cat.id"
          class="day-card__income-list-item">
          {{ cat.name }} - {{ currencyFilter(cat['total']) }}
        </li>
      </ul>
    </section>
    <section class="day-card__section day-card__section-outcome red lighten-3">
      <div class="center-align day-card__income-list-item"><strong>Вне плана {{
          currencyFilter(allNotPlannedRecordsSum)
        }}</strong></div>
      <ul class="center-align day-card__income-list">
        <li
          v-for="cat in notPlannedCategoriesWithSum"
          :key="cat.id"
          class="day-card__income-list-item">
          {{ cat.name }} - {{ currencyFilter(cat['total']) }}
        </li>
      </ul>
    </section>
    <footer class="day-card__footer">
      <div class="valign-center">
        <div class="col s12">
          <div
            :class="{'red lighten-4': balanceAtTheEndOfTheDay < 0, 'green': balanceAtTheEndOfTheDay >= 0}"
            class="center-align day-card__balance"><strong>Вечером {{
              currencyFilter(balanceAtTheEndOfTheDay)
            }}</strong></div>
        </div>
      </div>
    </footer>
  </article>
</template>

<script lang="ts" setup>
import { useFiltersStore } from '@/stores/filters';
import { Transaction, useTransactionsStore } from '@/stores/transactions';
import { NewCategory, useCategoriesStore } from '@/stores/categories';
import { usePlanningStore } from '@/stores/limits';
import { useAccountsStore } from '@/stores/accounts';
import { DayCard } from '@/components/CalendarGridComponent.vue';
import { computed, ComputedRef } from 'vue';
import { storeToRefs } from 'pinia';

const props = defineProps<{
  day: DayCard
}>();

const { dateFilter, currencyFilter } = useFiltersStore();
const { transactionsSortedByDate } = storeToRefs(useTransactionsStore());
const { categoriesAvailableForLimitation } = storeToRefs(useCategoriesStore());
const { limits } = storeToRefs(usePlanningStore());
const { accounts } = storeToRefs(useAccountsStore());

const todayFilter: number = new Date(props.day.year, props.day.month, props.day.day).getTime();
const prevMonthLastDate: number = new Date(props.day.year, props.day.month, 0).getDate();
const prevMonthFilter: number = new Date(props.day.year, props.day.month - 1, prevMonthLastDate).getTime();

const balanceTillToday: ComputedRef<number> = computed(() => {
  return accounts.value.map((acc) => {
    const incomeSumTillToday: number = transactionsSortedByDate.value
      .filter((rec) => rec.accountId === acc.id)
      .filter((rec) => rec.type === 'income')
      .filter((rec) => rec.date < todayFilter)
      .reduce((total, record) => Number((total + record.amount).toFixed(2)), 0);

    const outcomeSumTillToday: number = transactionsSortedByDate.value
      .filter((rec) => rec.accountId === acc.id)
      .filter((rec) => rec.type === 'outcome')
      .filter((rec) => rec.date < todayFilter)
      .reduce((total, record) => Number((total - record.amount).toFixed(2)), 0);

    const resultSumTillToday: number = acc.initialBalance + incomeSumTillToday + outcomeSumTillToday;

    return {
      ...acc,
      resultSumTillToday
    };
  })
    .reduce((total, acc) => Number((total + acc.resultSumTillToday).toFixed(2)), 0);
});

const balanceAtTheEndOfTheDay: ComputedRef<number> = computed(() => Number((balanceTillToday.value + allIncomeRecordsSum.value - allOutcomeRecordsSum.value - allNotPlannedRecordsSum.value)));


const allCategoriesOverspendSumTillToday: ComputedRef<number> = computed(() =>
  categoriesAvailableForLimitation.value.map((cat) => {
    const spendTillToday: number = transactionsSortedByDate.value
      .filter((tr) => tr.date > prevMonthFilter)
      .filter((tr) => tr.date <= todayFilter)
      .filter((tr) => !tr.isNotPlanned)
      .filter((tr) => tr.categoryId === cat.id)
      .reduce((total, record) => Number((total + record.amount).toFixed(2)), 0);

    const limit: number = <number>limits.value.find((lim) => lim.categoryId === cat.id)?.limit;

    const overspend: number = (spendTillToday - limit) > 0 ? Number((spendTillToday - limit).toFixed(2)) : 0;

    return {
      ...cat,
      overspend
    };
  })
    .filter((cat) => cat.overspend > 0)
    .reduce((total, cat) => Number((total + cat.overspend).toFixed(2)), 0));

const allTodayRecords: ComputedRef<Transaction[]> = computed(() =>
  transactionsSortedByDate.value
    .filter((tr) => tr.type !== 'transfer')
    .filter((tr) => tr.date === todayFilter));

const allIncomeRecordsSum: ComputedRef<number> = computed(() =>
  allTodayRecords.value
    .filter((rec) => rec.type === 'income')
    .reduce((total, record) => Number((total + record.amount).toFixed(2)), 0));

const allOutcomeRecords: ComputedRef<Transaction[]> = computed(() => allTodayRecords.value.filter((rec) => rec.type === 'outcome').filter((rec) => !rec.isNotPlanned));

const allOutcomeRecordsSum: ComputedRef<number> = computed(() => allOutcomeRecords.value.reduce((total, record) => Number((total + record.amount).toFixed(2)), 0));

const allNotPlannedRecordsSum: ComputedRef<number> = computed(() =>
  allTodayRecords.value
    .filter((rec) => rec.type === 'outcome')
    .filter((tr) => tr.isNotPlanned)
    .reduce((total, record) => Number((total + record.amount).toFixed(2)), 0));

const notPlannedRecordsSumTillToday: ComputedRef<number> = computed(() =>
  transactionsSortedByDate.value
    .filter((tr) => tr.isNotPlanned)
    .filter((tr) => tr.date > prevMonthFilter)
    .filter((tr) => tr.date <= todayFilter)
    .reduce((total, record) => {
      return Number((total + record.amount).toFixed(2));
    }, 0));

const outcomeCategoriesWithSum: ComputedRef<NewCategory[]> = computed(() =>
  categoriesAvailableForLimitation.value.map((cat) => {
    const total: number = allTodayRecords.value
      .filter((tr) => !tr.isNotPlanned)
      .filter((tr) => tr.categoryId === cat.id)
      .reduce((total, record) => {
        return Number((total + record.amount).toFixed(2));
      }, 0);

    return {
      ...cat,
      total
    };
  })
    .filter((cat) => cat.total !== 0));

const notPlannedCategoriesWithSum: ComputedRef<NewCategory[]> = computed(() =>
  categoriesAvailableForLimitation.value.map((cat) => {
    const total: number = allTodayRecords.value
      .filter((tr) => tr.isNotPlanned)
      .filter((tr) => tr.categoryId === cat.id)
      .reduce((total, record) => {
        return Number((total + record.amount).toFixed(2));
      }, 0);

    return {
      ...cat,
      total
    };
  })
    .filter((cat) => cat.total !== 0));
</script>

<style lang="scss" scoped>
.day-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  &.card-panel {
    margin: 0;
    padding: 12px 6px;
    height: 100%;
  }

  &.border-red {
    border: 2px solid red;
    border-radius: 4px;
  }

  & .row {
    margin-bottom: 10px;

    & .col {
      padding: 0;
    }
  }

  &__header {
    margin-bottom: 20px;
    min-height: 80px;
  }

  &__date {
    font-weight: bold;
  }

  &__section {
    margin-bottom: 20px;
  }

  &__footer {
    justify-self: flex-end;
    margin-top: auto;
  }
}
</style>