<template>
  <div class="input-field">
    <select ref="yearSelect" v-model.number="transactionsYearFilter">
      <option v-for="year in yearsSet" :key="year" :value="year">{{ year }}</option>
    </select>
    <label>Фильтрация по годам</label>
  </div>
</template>

<script lang="ts" setup>
import { computed, ComputedRef, onMounted, ref } from 'vue';
import { Transaction, useTransactionsStore } from '@/stores/transactions';
import { storeToRefs } from 'pinia';

const { transactionsSortedByDate, transactionsYearFilter } = storeToRefs(useTransactionsStore());

const yearSelect = ref();
const yearSelectInstance = ref();

const yearsSet: ComputedRef<Set<number>> = computed((): Set<number> => {
  const years: Set<number> = new Set();
  years.add(new Date().getFullYear());
  if (!!transactionsSortedByDate.value) {
    transactionsSortedByDate.value.forEach((tr: Transaction) => {
      const trYear: number = new Date(tr.date).getFullYear();
      years.add(trYear);
    });
    const yearsArr: number[] = Array.from(years);
    const maxYear: number = Math.max.apply(null, yearsArr) + 1;
    years.add(maxYear);
    return years;
  } else {
    return years;
  }
});

onMounted(() => {
  yearSelectInstance.value = M.FormSelect.init(yearSelect.value);
});
</script>