<template>
  <ul class="calendar-list">
    <li
      v-for="day in daysOfMonth"
      :key="day.id"
      :style="{ 'grid-area': day.weekdayName, 'grid-row': day.week }"
      class="calendar-list__item">
      <CalendarDayCardComponent :day="day"/>
    </li>
  </ul>
</template>

<script lang="ts" setup>
import CalendarDayCardComponent from '@/components/CalendarDayCardComponent.vue';
import { storeToRefs } from 'pinia';
import { computed, ComputedRef } from 'vue';
import { useTransactionsStore } from '@/stores/transactions';

const { transactionsMonthFilter, transactionsYearFilter } = storeToRefs(useTransactionsStore());

const weekdays: string[] = [ 'Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa' ];

const lastDate: ComputedRef<number> = computed(() => new Date(transactionsYearFilter.value, transactionsMonthFilter.value + 1, 0).getDate());

export interface DayCard {
  day: number,
  month: number,
  year: number,
  week: number,
  weekday: number,
  weekdayName: string,
  id: number
}

const daysOfMonth: ComputedRef<DayCard[]> = computed(() => {
  const days: DayCard[] = [];
  let weekCounter: number = 1;
  for (let i = 1; i <= lastDate.value; i++) {
    const day: DayCard = { id: 0, week: 0, day: 0, month: 0, weekday: 0, weekdayName: '', year: 0 };
    day.day = i;
    day.month = transactionsMonthFilter.value;
    day.year = transactionsYearFilter.value;
    day.weekday = new Date(transactionsYearFilter.value, transactionsMonthFilter.value, i).getDay();
    day.weekdayName = weekdays[day.weekday];
    day.week = weekCounter;
    day.id = new Date(day.year, day.month, day.day).getTime();
    days.push(day);
    if (day.weekday === 0) weekCounter++;
  }
  return days;
});
</script>

<style lang="scss" scoped>
.calendar-list {
  display: grid;
  grid-gap: 20px;
  grid-template-areas:
    "Mo Tu We Th Fr Sa Su";
  align-items: stretch;

  @media (max-width: 1280px) {
    grid-gap: 16px;
  }

  @media (max-width: 1200px) {
    grid-template-areas: unset;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 12px;
  }

  @media (max-width: 998px) {
    grid-template-columns: repeat(4, 1fr);
  }

  @media (max-width: 768px) {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
  }

  @media (max-width: 600px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr;
  }

  &__item {
    @media (max-width: 1200px) {
      grid-area: unset !important;
    }
  }
}
</style>